import React, { useState, useRef } from 'react';
import { YMInitializer } from 'react-yandex-metrika';
import convertText from '../helpers';
import Notification from '../components/Notification';
import SEO from '../components/Seo';
import Comments from '../components/Comments';
import 'normalize.css';
import './styles.css';

function App() {
  const textareaRef = useRef(null);
  const [text, setText] = useState('');
  const [convertedText, setConvertedText] = useState('');
  const [disabled, setDisabled] = useState(true);
  const [notificationState, setNotificationState] = useState(false);

  const showNotification = (delay = 3000) => {
    setNotificationState(true);
    setTimeout(() => {
      setNotificationState(false);
    }, delay);
  };

  const handleTextChange = event => {
    setText(event.target.value);
  };

  const handleCovert = event => {
    event.preventDefault();
    const newConvertedText = convertText(text);
    setConvertedText(newConvertedText);
    setDisabled(false);
  };

  const handleCopy = event => {
    event.preventDefault();
    textareaRef.current.select();
    document.execCommand('copy');
    showNotification();
  };

  const handleTextareaFocus = event => {
    event.target.select();
  };

  return (
    <div>
      <YMInitializer accounts={[54322950]} />
      <SEO
        title="    ?"
        description="      ?      ."
        author="gornyyvladimir"
      />
      <Notification show={notificationState}>
         !    Instagram.
      </Notification>
      <div>
        <h1 className="title">    ?</h1>
        <p className="text">
                  ,
                   .
        </p>
        <div className="wrapper">
          <form className="form" onSubmit={handleCovert}>
            <label className="label" htmlFor="text">
              ,    
              <textarea
                id="text"
                className="textarea"
                onChange={handleTextChange}
                value={text}
                placeholder=",  "
                rows="5"
                cols="30"
                required
              />
            </label>
            <button className="button" type="submit">
              
            </button>
          </form>
          <form className="form" onSubmit={handleCopy}>
            <label className="label" htmlFor="converted-text">
               
              <textarea
                id="converted-text"
                className="textarea"
                ref={textareaRef}
                onFocus={handleTextareaFocus}
                value={convertedText}
                placeholder="   "
                rows="5"
                cols="30"
                readOnly
              />
            </label>
            <button className="button" type="submit" disabled={disabled}>
              
            </button>
          </form>
        </div>
        <p className="text text--margin-bottom-2">
           : <b>{text.length}</b>
          <br />
               Instagram: <b>2200</b>
        </p>
        <Comments />
      </div>
    </div>
  );
}

export default App;
